 .card {
    border: 1px solid hsl(0, 0%, 80%);
    background-color: rgb(224, 245, 224);;
    border-radius: 10px;
    /* padding: 20px;
    margin: 10px; */
    text-align: center;
    max-width: 310px;
    display: inline-block;
    box-shadow: 5px 5px 5px hsla(0, 0%, 0%, 0.1)
}

.card .card-img-top{
    max-width: 100%;
    height: auto;
    margin-bottom: 10px;
    
}

.card .card-title{
    font-family: Arial, sans-serif;
    margin: 0;
    color: black;
}

.card .card-text{
    font-family: Arial,sans-serif;
    color: gray;
}